.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card img {
    width: 200px;
    height: 200px;
}


.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.jumbotron-fluid {
  background-image: url("/images/pink.jpg") !important; 
  background-size: cover;
  width: 100%; 
  height: 200px;
  text-align: center; 
  margin-bottom: 0;
}  
.navbar {
    background-color: blueviolet !important; 
    font-size: 2em;
    font-weight: bold;
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

